<template>
  <div>
      <h1 class="title-form-contact">Contacto</h1>
      <b-container class="form-contact">
        <!-- Nombre !-->
        <label for="name">Nombre</label>
        <b-form-input 
        id="name"
        class="mb-2" 
        type="text" 
        v-model="name"
        :state="nameState" 
        placeholder="Ingrese nombre">
        </b-form-input>


        <!-- Email !-->
        <label for="email">Correo electrónico</label>
        <b-form-input
        id="email"
        v-model="email" 
        class="mb-2" 
        type="email" 
        :state="emailState" 
        placeholder="Ingrese correo electrónico">
        </b-form-input>


        <!-- Mensaje !-->
        <label for="msg">Ingrese mensaje</label>
        <b-form-input 
        id="msg"
        class="mb-2" 
        v-model="msg"
        type="text"
        :state="msgState"  
        placeholder="Mensaje" 
        max-lenght="250">
        </b-form-input>

        
        <a class="btn" id="btn-enviar-form">Enviar</a>
      </b-container>
  </div>
</template>

<script>
export default {
  data(){
    return{
      name: '',
      email: '',
      msg: ''
    }
  },
  computed:{
    nameState(){
      //Valida que haya algo escrito
      if(this.name.length == 0){
        return null
      }

      //Valida que solo tenga letras
      let letras = /^[A-Z]+$/i;
      var validacionName = letras.test(this.name)

      return validacionName == true ? true : false
    },
    emailState(){
      //Valida que haya algo escrito
      if(this.email.length == 0){
        return null
      }

      //Valida que haya @ y .
      let expReg = /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/;
      var validacionEmail = expReg.test(this.email)

      return validacionEmail == true ? true : false
    },
    msgState(){
       //Valida que haya algo escrito
      if(this.msg.length == 0){
        return null
      }
      return this.msg.length >= 20 ? true : false
    }
  }

}
</script>

<style>
.form-contact{
  background: #D94A3D;
  border: 3px solid #D94A3D;
  border-radius: 1.50rem;
  color: #F1F1F1;
  padding: 2rem;
  margin-top: 1rem;
}

.title-form-contact{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3rem;
  color: #FFFF;
}

#btn-enviar-form{
  background: #1D2026;
  cursor: pointer;
  font-weight: 650;
  font-size: .85rem;
  text-transform: uppercase;
  color: #FFF;
  padding: .50rem;
  margin-top: 1rem;
}
</style>